import "./styles.css";

import * as THREE from "three";
import random from "canvas-sketch-util/random";
import palettes from "nice-color-palettes";
import eases from "eases";
import BezierEasing from "bezier-easing";

function main() {
  const canvas = document.querySelector("#c");
  const renderer = new THREE.WebGLRenderer({ canvas });

  const fov = 75;
  const aspect = 2; // the canvas default
  const near = 0.1;
  const far = 5;
  const camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
  camera.position.z = 2;

  renderer.setClearColor("hsl(0%, 0%, 95%)", 1);

  const scene = new THREE.Scene();

  {
    const color = 0xffffff;
    const intensity = 1;
    const light = new THREE.DirectionalLight("white", intensity);
    light.position.set(1, 2, 4);
    scene.add(light);
  }

  const ambientLight = new THREE.AmbientLight("hsl(0, 0, 80%)", 1);
  scene.add(ambientLight);

  const palette = random.pick(palettes);

  const boxWidth = 1;
  const boxHeight = 1;
  const boxDepth = 1;
  const geometry = new THREE.BoxGeometry(boxWidth, boxHeight, boxDepth);

  const bezierFn = BezierEasing(0, 0, 1, 1);

  for (let i = 0; i < 100; i++) {
    const mesh = new THREE.Mesh(
      geometry,
      new THREE.MeshStandardMaterial({
        color: random.pick(palette),
        wireframe: false
      })
    );
    mesh.position.set(
      random.range(1, -1),
      random.range(1, -1),
      random.range(1, -1)
    );

    mesh.scale.set(
      random.range(1, -1),
      random.range(1, -1),
      random.range(1, -1)
    );
    mesh.scale.multiplyScalar(0.09);
    scene.add(mesh);
  }

  function resizeRendererToDisplaySize(renderer) {
    const canvas = renderer.domElement;
    const pixelRatio = window.devicePixelRatio;
    const width = (canvas.clientWidth * pixelRatio) | 0;
    const height = (canvas.clientHeight * pixelRatio) | 0;
    const needResize = canvas.width !== width || canvas.height !== height;
    if (needResize) {
      renderer.setSize(width, height, false);
    }
    return needResize;
  }

  function render(time) {
    time *= 0.001;

    if (resizeRendererToDisplaySize(renderer)) {
      const canvas = renderer.domElement;
      camera.aspect = canvas.clientWidth / canvas.clientHeight;
      camera.updateProjectionMatrix();
    }

    scene.rotation.x = time * 0.09;
    scene.rotation.y = time;
    scene.rotation.z = time * 0.09;
    scene.position.z = bezierFn(time * random.range(-0.004, 0.004));
    renderer.render(scene, camera);

    requestAnimationFrame(render);
  }

  requestAnimationFrame(render);
}

main();
